import './styles.scss';
import React, { useState } from 'react';
import Modal from 'react-modal';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { loadSelectionCode } from '../../store/CodePanel/actions';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';
import { setIsOpen as setIsOpenAction } from '../../store/DossierSelectionPopUp/actions';
import { dossierUrlRegExp } from '../../constants/dossier-urls';
import { exampleTemplates } from '../../utils/templateGenerator';
import waves from './waves.png';
import logo from './logo.png';
import { sendGAEvent } from '../../store/GoogleAnalytics/actions';
import {
  USE_CUSTOM_DOSSIER,
  USE_DEMO_EXAMPLES,
  CLICK_PLAYGROUND_USER_MANUAL_LINK,
} from '../../constants/google-analytics';
import {
  inactiveBtn,
  activeBtn,
  inactiveError,
  activeError,
  regexErrorMessage,
  reminderMessage,
} from '../../constants/dossier-selection-popup';

const DossierSelectionPopUp = ({
  isDarkTheme,
  isOpen,
  setIsOpen,
  onLoadSelectionCode,
  onSendGAEvent,
}) => {
  // Handles states for tab selection
  const customUrlTab = 'custom-url-tab';
  const demoTab = 'demo-tab';
  const [selectedTab, setSelectedTab] = useState(demoTab);

  // Handles states for example selection
  const [selectedExample, setSelectedExample] = useState({
    url: exampleTemplates.general.items.simpleExample.defaultUrl,
    name: exampleTemplates.general.items.simpleExample.name,
    groupName: exampleTemplates.general.groupName,
  });
  const [showDropDown, setShowDropDown] = useState(false);

  // Handles states for custom dossier selection
  const [customUrl, setCustomUrl] = useState('');
  const [err, setErr] = useState(inactiveError);
  const [btn, setBtn] = useState(inactiveBtn);
  const [placeholderActive, setPlaceholderActive] = useState(false);

  // Checks if the url is valid
  const checkValidUrl = (url) => {
    return dossierUrlRegExp.test(url);
  };
  // Handles the input for custom dossier selection and loads in the simpleExample template with the inputted url
  const sendInput = (url) => {
    if (checkValidUrl(url)) {
      setIsOpen(false);
      setErr(inactiveError);
      onLoadSelectionCode(
        url,
        exampleTemplates.general.items.simpleExample.name,
        exampleTemplates.general.groupName,
      );
    } else {
      setErr(activeError);
    }
  };

  // Generates the content for each tab
  const mstrPopupContent = () => {
    if (selectedTab === demoTab) {
      const templateObjects = Object.values(exampleTemplates);
      return (
        <section className="dossier-selection-container__content__form">
          <span className="dossier-selection-dropdown-label">Select Example</span>
          <div
            className="dossier-selection-dropdown"
            tabIndex="0"
            onBlur={() => setShowDropDown(false)}
          >
            <div
              className={
                showDropDown
                  ? 'open dossier-selection-dropdown__content'
                  : 'dossier-selection-dropdown__content'
              }
              role="button"
              onClick={() => setShowDropDown(!showDropDown)}
            >
              <span className="dossier-selection-dropdown__content--selected">
                {selectedExample.name}
              </span>
              <span
                className={
                  showDropDown
                    ? 'open dossier-selection-dropdown-arrow'
                    : 'dossier-selection-dropdown-arrow'
                }
              />
            </div>
            <div className="dossier-selection-dropdown__container" role="menu">
              <ul
                role="group"
                className={
                  showDropDown
                    ? 'open dossier-selection-dropdown-group'
                    : 'dossier-selection-dropdown-group'
                }
              >
                {templateObjects.map((templateGroup) => {
                  if (templateGroup.items) {
                    const templateGroupItems = Object.values(templateGroup.items);
                    const groupOptions = templateGroupItems.map((template) => {
                      if (selectedExample.name === template.name) {
                        return (
                          <li
                            className="dossier-selection-dropdown-group__group__option--selected"
                            key={template.name}
                            role="menuitemradio"
                            aria-checked="true"
                            onClick={() => setShowDropDown(false)}
                          >
                            {template.name}
                          </li>
                        );
                      }
                      return (
                        <li
                          className="dossier-selection-dropdown-group__group__option"
                          role="menuitemradio"
                          aria-checked="false"
                          key={template.name}
                          onClick={() => {
                            setShowDropDown(false);
                            setSelectedExample({
                              url: template.defaultUrl,
                              name: template.name,
                              groupName: templateGroup.groupName,
                            });
                          }}
                        >
                          {template.name}
                        </li>
                      );
                    });
                    return (
                      <ul
                        className="dossier-selection-dropdown-group__group"
                        key={templateGroup.groupName}
                      >
                        <li className="dossier-selection-dropdown-group__group__header">
                          {templateGroup.groupName}
                        </li>
                        {groupOptions}
                      </ul>
                    );
                  }
                  return null;
                })}
              </ul>
            </div>
          </div>
          <button
            className="dossier-selection-container__button"
            onClick={() => {
              setIsOpen(false);
              onLoadSelectionCode(
                selectedExample.url,
                selectedExample.name,
                selectedExample.groupName,
              );
              onSendGAEvent(USE_DEMO_EXAMPLES, selectedExample.name);
            }}
            type="button"
          >
            Continue
          </button>
        </section>
      );
    }
    return (
      <section className="dossier-selection-container__content__form">
        <span className={placeholderActive || customUrl ? 'placeholder active' : 'placeholder'}>
          {' '}
          Dossier Url
        </span>
        <input
          type="text"
          id="url"
          className="url-input"
          tabIndex="0"
          value={customUrl}
          onFocus={() => setPlaceholderActive(true)}
          onBlur={() => setPlaceholderActive(false)}
          onChange={(e) => {
            setCustomUrl(e.target.value);
            /**
             * If the value is non empty, determine if the value is valid.
             * If valid, hide regex error and allow continue button.
             * If invalid, show regex erorr and disallow continue button.
             * If the value is empty, hide regex error and disallow continue button.
             */
            if (e.target.value) {
              if (checkValidUrl(e.target.value)) {
                setBtn(activeBtn);
                setErr(inactiveError);
              } else {
                setBtn(inactiveBtn);
                setErr(activeError);
              }
            } else {
              setBtn(inactiveBtn);
              setErr(inactiveError);
            }
          }}
        />

        <div className="error-message">
          <p className={err}>{regexErrorMessage}</p>
          <p className={err === activeError ? inactiveError : activeError}>
            {reminderMessage}
            <a
              href="https://github.com/MicroStrategy/playground#requirements-for-embedding"
              target="_blank"
              rel="noreferrer"
              onClick={() => {
                onSendGAEvent(CLICK_PLAYGROUND_USER_MANUAL_LINK);
              }}
            >
              user manual
            </a>
            .
          </p>
        </div>
        <button
          id="btnContinue"
          className={btn}
          onClick={() => {
            sendInput(customUrl);
            onSendGAEvent(USE_CUSTOM_DOSSIER, customUrl);
          }}
          type="button"
        >
          Continue
        </button>
      </section>
    );
  };

  const mstrPopup = () => (
    <div id="dossier-selection-container">
      <img alt="waves" src={waves} />
      <div className="dossier-selection-container__title">
        <img alt="" src={logo} width="70px" />
        <div className="dossier-selection-container__title__content">Embedding SDK Playground</div>
        <div
          className={selectedTab === demoTab ? 'tab selected' : 'tab'}
          onClick={() => setSelectedTab(demoTab)}
        >
          Examples
        </div>
        <div
          className={selectedTab === customUrlTab ? 'tab selected' : 'tab'}
          onClick={() => setSelectedTab(customUrlTab)}
        >
          Your Dossier
        </div>
      </div>
      <div className="dossier-selection-container__content">{mstrPopupContent()}</div>
    </div>
  );

  return (
    <Modal
      appElement={document.getElementById('root')}
      className={isDarkTheme ? 'dossier-selection dark' : 'dossier-selection'}
      overlayClassName="dossier-selection-overlay"
      isOpen={isOpen}
      onRequestClose={() => {
        setIsOpen(false);
      }}
    >
      {mstrPopup()}
    </Modal>
  );
};

DossierSelectionPopUp.propTypes = {
  isDarkTheme: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  onLoadSelectionCode: PropTypes.func.isRequired,
  setIsOpen: PropTypes.func.isRequired,
  onSendGAEvent: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
  return {
    isDarkTheme: selectDarkToggle(state),
  };
}
function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      onLoadSelectionCode: loadSelectionCode,
      setIsOpen: setIsOpenAction,
      onSendGAEvent: sendGAEvent,
    },
    dispatch,
  );
}
export default connect(mapStateToProps, mapDispatchToProps)(DossierSelectionPopUp);
