import { dossierUrlRegExp } from '../constants/dossier-urls';
// Modifies the given text to replace all strings in the array oldValues with the strings in newValues
const textReplaceAll = (text, oldValues, newValues) => {
  if (text) {
    let output = text;
    for (let i = 0; i < oldValues.length; i += 1) {
      output = output.replaceAll(oldValues[i], newValues[i]);
    }
    return output;
  }
  return text;
};
// Parse the dossier url and return the object {baseServerUrl, libraryName, and embeddinglibjsUrl}
export const parseDossierUrl = (url) => {
  const urlMatched = url.match(dossierUrlRegExp);
  const baseServerUrl = urlMatched?.groups?.baseServerUrl || '';
  const libraryName = urlMatched?.groups?.libraryName || '';
  const embeddinglibjsUrl = `${baseServerUrl}/${libraryName}/javascript/embeddinglib.js`;

  return {
    baseServerUrl,
    libraryName,
    embeddinglibjsUrl,
  };
};
// Creates the html string given location of the template and can replace the defaultUrl in template along with the
// embeddinglibjs url
// (location: string required rest are optional, defaultUrl: string, url: string,)
const createTemplate = (location, defaultUrl = null, url = null) => {
  if (defaultUrl && url) {
    const defaultParsedUrl = parseDossierUrl(defaultUrl); // Returns an object containing {baseServerUrl, libraryName, and embeddinglibjsUrl} from given url
    const parsedUrl = parseDossierUrl(url);
    const currentStrings = [defaultUrl, defaultParsedUrl.embeddinglibjsUrl];
    const newStrings = [url, parsedUrl.embeddinglibjsUrl];

    // Outputs the content of the text file after replacing the currentStrings with newStrings
    return fetch(location)
      .then((response) => response.text())
      .then((text) => textReplaceAll(text, currentStrings, newStrings));
  }
  // Outputs the content of the text file
  return fetch(location).then((response) => response.text());
};

