// Function that generates the initial code for the template
export const templateInitialCode = (
  url = exampleTemplates.general.items.simpleExample.defaultUrl,
  exampleName = exampleTemplates.general.items.simpleExample.name,
  exampleGroupName = exampleTemplates.general.groupName,
) => {
  let initialCode = '';

  const BreakException = {};

  try {
    // Loop through the exampleTemplates to find the specified template based on exampleGroupName and exampleName
    // then get the initial code for that template
    Object.values(exampleTemplates).forEach((exampleGroup) => {
      if (exampleGroup.groupName === exampleGroupName && exampleGroup.items) {
        Object.values(exampleGroup.items).forEach((example) => {
          if (example.name === exampleName) {
            if (example.defaultUrl !== url) {
              // Replace the defaultUrl in template using the new url provided by user
              initialCode = example.template(example.defaultUrl, url);
              throw BreakException;
            } else {
              // Use the defaultUrl in template used for demos
              initialCode = example.template();
              throw BreakException;
            }
          }
        });
      }
    });
  } catch (e) {
    if (e !== BreakException) {
      throw e;
    }
  }

  return initialCode;
};
