import { combineReducers } from 'redux';
import initialState from './initialState';
import codePanelReducer from './store/CodePanel/reducer';
import operationsMenuReducer from './store/OperationsMenu/reducer';
import navigationBarReducer from './store/NavigationBar/reducer';
import previewPanelReducer from './store/PreviewPanel/reducer';
import dossierSelectionPopUpReducer from './store/DossierSelectionPopUp/reducer';
import eventPanelReducer from './store/EventPanel/reducer';

const allSubReducers = combineReducers({
  CodePanel: codePanelReducer,
  OperationsMenu: operationsMenuReducer,
  NavigationBar: navigationBarReducer,
  PreviewPanel: previewPanelReducer,
  DossierSelectionPopUp: dossierSelectionPopUpReducer,
  EventPanel: eventPanelReducer,
});

// eslint-disable-next-line default-param-last -- this is Redux's way, ignore it
const rootReducer = (state = initialState, action) => {
  switch (action.type) {
    default:
      return allSubReducers(state, action);
  }
};

export default rootReducer;
