import React, { useRef, useEffect } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import PropTypes from 'prop-types';

import { previewPanel } from '../../constants';
import './styles.scss';
import { selectContent } from '../../store/CodePanel/selectors';
import { updateEventContent } from '../../store/EventPanel/actions';
import { setReload } from '../../store/PreviewPanel/actions';
import { selectReload } from '../../store/PreviewPanel/selectors';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';

// Component containing the Iframe of which the code from code editor will be executed in. Would show the preview/result
// of the executed code/embedding dossier.
const PreviewPanel = ({ content, reload, isDarkTheme, onUpdateEventContent, onSetReload }) => {
  const IFRAME_REF = useRef();
  const consoleListenerScript = `
  <script>
  window.console = {
    log: function(){
      window.parent.console.log.apply(console, arguments)
    },
    error: function() {
      window.parent.console.error.apply(console, arguments)
    }
  };
  window.onerror = function() {
    console.error.apply(console, arguments)
  }
  </script>
`;
  const loadEventListenersScript = `
    try{
      function updateEventPanel(evt){
        let text = this.event.data.name;
        window.parent.postMessage([text, evt], '*');
      }

      function registerEvents(interval) {
        clearInterval(interval)
        let eventNames = Object.values(microstrategy.dossier.EventType)
        let eventKeys = Object.keys(microstrategy.dossier.EventType).map((key) => "microstrategy.dossier.EventType."+key)
        for (const eventName of eventNames){
          dossier.registerEventHandler(eventName, updateEventPanel)
        }
        console.log("Successfully registered event listeners for Event Handler:", eventKeys)
      }
      // Try to register the event listeners every .1 seconds and give up after 100 tries
      const attachEventListeners = () => {
        var count = 0;
        const interval = setInterval(() => {
          if (count++ >= 100) {
            console.log("Failed to register event listeners for Event Handler: 'dossier' was undefined")
            clearInterval(interval)
          }
          else if (typeof dossier !== 'undefined' && dossier){
            try {
              registerEvents(interval)
            } catch(error) {
              console.log("Failed to register event listeners for Event Handler:", error)
            }
          }
        }, 100)

      }
      attachEventListeners()
    } catch(error) {
      console.log("Failed to register event listeners:", error)
    }
  `;
  const loadEventListeners = () => {
    if (IFRAME_REF.current && IFRAME_REF.current.contentWindow) {
      const script = IFRAME_REF.current.contentWindow.document.createElement('script');
      script.innerHTML = loadEventListenersScript;
      IFRAME_REF.current.contentWindow.document.body.appendChild(script);
    }
  };
  const srcDocContent = () => {
    if (content) {
      return consoleListenerScript + content;
    }
    return consoleListenerScript;
  };

  useEffect(() => {
    function receiveMessage(e) {
      const iframe = IFRAME_REF.current;
      if (iframe && iframe.contentWindow && e.source === iframe.contentWindow) {
        let text = `${e.data[0]} Event\n`;

        if (e.data[1]) {
          text += `${JSON.stringify(e.data[1], null, 2)}\n`;
        }
        text += '\n';
        onUpdateEventContent(text);
      }
    }
    window.addEventListener('message', receiveMessage);
    return () => {
      window.removeEventListener('message', receiveMessage);
    };
  });

  useEffect(() => {
    if (reload) {
      IFRAME_REF.current?.contentWindow?.location?.reload();
      onSetReload(false);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps -- `onSetReload` should not be passed into the dependencies array
  }, [reload]);

  return (
    <div className={isDarkTheme ? 'preview-panel dark' : 'preview-panel'}>
      <iframe
        className="embedding-iframe card-shadow "
        title={previewPanel.IFRAME_TITLE}
        id={previewPanel.IFRAME_ID}
        srcDoc={srcDocContent()}
        onLoad={() => {
          if (!!content && content !== '') {
            loadEventListeners();
          }
        }}
        ref={IFRAME_REF}
        scrolling="yes"
      />
    </div>
  );
};

PreviewPanel.propTypes = {
  content: PropTypes.string.isRequired,
  reload: PropTypes.bool.isRequired,
  isDarkTheme: PropTypes.bool.isRequired,
  onUpdateEventContent: PropTypes.func.isRequired,
  onSetReload: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
  return {
    content: selectContent(state),
    reload: selectReload(state),
    isDarkTheme: selectDarkToggle(state),
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      onUpdateEventContent: updateEventContent,
      onSetReload: setReload,
    },
    dispatch,
  );
}
export default connect(mapStateToProps, mapDispatchToProps)(PreviewPanel);
