import React from 'react';
import ReactTooltip from 'react-tooltip';

// ...props will override defaults
const ButtonTooltip = (props) => {
  return (
    <ReactTooltip
      id="placeholder"
      className="react-tooltip"
      place="bottom"
      effect="solid"
      border={false}
      clickable
      multiline
      // eslint-disable-next-line react/no-unstable-nested-components -- TODO: find better way to represent this
      getContent={() => <div className="tooltip-text">Place holder text</div>}
      // eslint-disable-next-line react/jsx-props-no-spreading -- need props spreading to override defaults
      {...props}
    />
  );
};

export default ButtonTooltip;
