import React from 'react';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import './styles.scss';
import { selectDarkToggle } from '../../store/NavigationBar/selectors';
import { updateTheme, saveTheme } from '../../store/NavigationBar/actions';
import logo from './sitelogo.svg';
import { setIsOpen } from '../../store/DossierSelectionPopUp/actions';
import useAuth from '../../hooks/useAuth';
import oidcConfig from '../../oidcConfig';
import { sendGAEvent } from '../../store/GoogleAnalytics/actions';
import { CLICK_LOGOUT_BUTTON, TOGGLE_LIGHT_DARK_THEME } from '../../constants/google-analytics';

// Component containing the navigation bar with sign up options
const NavigationBar = ({
  onUpdateTheme,
  isDarkTheme,
  onSaveTheme,
  setIsPopUpOpen,
  onSendGAEvent,
}) => {
  const auth = useAuth();

  const onLogout = () => {
    onSendGAEvent(CLICK_LOGOUT_BUTTON);
    auth.signout();
  };

  return (
    <div className={isDarkTheme ? 'navigation-bar dark' : 'navigation-bar'}>
      <header>
        <div className={isDarkTheme ? 'menu dark' : 'menu'}>
          <div className="logo">
            <a href="https://www.microstrategy.com/en" target="_blank" rel="noreferrer">
              <img alt="" src={logo} />
            </a>
          </div>
          <div className="title">Embedding SDK Playground (Beta)</div>
          <button
            data-tip
            data-for="button__start-over__tooltip"
            className="button__start-over"
            onClick={() => setIsPopUpOpen(true)}
            type="button"
          >
            <span className="material-icons">refresh</span>
            <p>Start over</p>
          </button>
          <div className="toggle">
            <label className="switch">
              <input
                type="checkbox"
                checked={isDarkTheme}
                onChange={(e) => {
                  onUpdateTheme(e.target.checked);
                  onSaveTheme(e.target.checked);
                  onSendGAEvent(TOGGLE_LIGHT_DARK_THEME);
                }}
              />
              <span className="slider round" />
            </label>
          </div>
          {oidcConfig.requireAuth && (
            <div className="button-logout" onClick={onLogout}>
              <span className="material-icons">logout</span>
              Logout
            </div>
          )}
        </div>
      </header>
    </div>
  );
};

NavigationBar.propTypes = {
  isDarkTheme: PropTypes.bool.isRequired,
  onUpdateTheme: PropTypes.func.isRequired,
  onSaveTheme: PropTypes.func.isRequired,
  setIsPopUpOpen: PropTypes.func.isRequired,
  onSendGAEvent: PropTypes.func.isRequired,
};

function mapStateToProps(state) {
  return {
    isDarkTheme: selectDarkToggle(state),
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      onUpdateTheme: updateTheme,
      onSaveTheme: saveTheme,
      setIsPopUpOpen: setIsOpen,
      onSendGAEvent: sendGAEvent,
    },
    dispatch,
  );
}

export default connect(mapStateToProps, mapDispatchToProps)(NavigationBar);
